import React, { Component } from "react";
import { getMovies } from "./services/fakeMovieService.js";
import Pagination from "./components/common/pagination.jsx";
import { paginate } from './utils/paginate';
import GenreList from "./components/common/genrelist.jsx";
import {getGenres} from "./services/fakeGenreService.js";
import MoviesTable from "./components/moviesTable.jsx";

import _ from 'lodash';

class Movies extends Component {
  state = {
    movies: getMovies(),
    genres: getGenres(),
    pageSize : 4,
    start : 0,
    endIndex : 4,
    currentPage : 1,
    sortColumn : {path:'title', order :'asc'}
  };

  //componentDidMount(){
  //   this.setState({movies : getMovies(), genres : getGenres()});
     
  //}

  handleDelete = (movie) => {
    const movies = this.state.movies.filter((m) => m._id !== movie._id);
    this.setState({ movies: movies });
  };
  handleLike = movie => {
    const movies = [...this.state.movies];
    const index = movies.indexOf(movie);
    movies[index] = {...movies[index]};
    movies[index].liked = !movies[index].liked;
    this.setState({movies});
  };
  handlePageChange = page => {
    this.setState({currentPage : page});
    
  };
   
  handleGenreSelect = (genre) => {
    this.setState({ selectedGenre : genre});
  };

  handleSort = (sortColumn) => {
    this.setState({sortColumn});
  }

  render() {
    const {length:count} = this.state.movies;
    const {pageSize , currentPage, selectedGenre, movies: allMovies, sortColumn} = this.state;
    
    const filtered = selectedGenre ? allMovies.filter(m => m.genre.name === selectedGenre) : allMovies; 
    
    const sorted = _.orderBy(filtered, [sortColumn.path], [sortColumn.order])

    const movies = paginate(sorted, currentPage, pageSize );
    

    return (
      <React.Fragment>
        <h1>Showing {filtered.length} movies in the database</h1>
        <h2>Movies List</h2>
        <br></br>
        
      <div className = "row">
      <div className="col-3">
          <GenreList items={this.state.genres}
                     selectedItem = {this.state.selectedGenre}
                     textProperty="name"
                     valueProperty="_id" 
                     onGenreSelect={this.handleGenreSelect}/>
      </div>
       <div className="col">   
        <MoviesTable movies = {movies}
          sortColumn = {sortColumn}
          onLike = {this.handleLike}
          onDelete = {this.handleDelete}
          onSort = {this.handleSort}/>     
        </div>          
      </div>
      <br></br>
      <div><Pagination    itemsCount={filtered.length}
                          pageSize={pageSize} 
                          onPageChange={this.handlePageChange}
                          currentPage={currentPage}/>
      </div>
      </React.Fragment>
    );
    
  }
}

export default Movies;
