import React, { Component } from 'react';
import _ from 'lodash';
import {getGenres} from "../../services/fakeGenreService.js";

const GenreList = (props) => {
    
    const {items, textProprty, valueProperty, selectedItem,onGenreSelect} = props;
   
    return(
        <ul className="list-group">
            {
            items.map(item => <li key={item[valueProperty]} className="list-group-item"><a className={ item.name === selectedItem ? 'list-group-item active' : 'list-group-item'}  onClick={() => onGenreSelect(item[textProprty])}>{item.name}</a></li>)
            }
        </ul>
    )
}

GenreList.defaultProps = {
  textProprty:'name',
  valueProperty:'_id'
}
export default GenreList;